@charset "UTF-8";
:root {
  --white: #fff;
  --black: #000;
  --black-100: #191B1E;
  --gray-100: #1E2125;
  --gray-200: #242B35;
  --gray-300: #28303A;
  --gray-400: #2D363F;
  --gray-500: #707D9B;
  --gray-600: #838C9D;
  --gray-700: rgba(112, 125, 155, 0.7);
  --gray-7: #D9D9D9;
  --red: #FF2929;
  --green: #14E40F;
}

:root {
  --font-family: "Inter", sans-serif;
  --light: 300;
  --regular: 400;
  --medium: 500;
  --bold: 700;
  --extra-bold: 800;
}

:root {
  --base-transition: all .34s ease-out;
}

/*==========  RESET STYLES  ==========*/
html {
  overflow-y: scroll;
}

* {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  -webkit-text-size-adjust: none;
  box-sizing: border-box;
}

html:not(.touch-screen)::-webkit-scrollbar,
html:not(.touch-screen) *::-webkit-scrollbar {
  width: 3px;
  height: 3px;
}

html:not(.touch-screen)::-webkit-scrollbar-track,
html:not(.touch-screen) *::-webkit-scrollbar-track {
  background: #3C4149;
}

html:not(.touch-screen)::-webkit-scrollbar-thumb,
html:not(.touch-screen) *::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.2);
}

/*==========  LOADER  ==========*/
#loader-wrapper {
  position: fixed;
  left: 0;
  top: -100px;
  right: 0;
  bottom: -100px;
  background: #ffffff;
  z-index: 2000;
}

/*-------------------------------------------------------------------------------------------------------------------------------*/
/* GLOBAL SETTINGS */
/*-------------------------------------------------------------------------------------------------------------------------------*/
html {
  height: 100%;
}

body {
  height: 100%;
}

#content-block {
  position: relative;
  transition: var(--base-transition);
}

#content-block.bg-gray {
  background-color: var(--gray-200);
}

#content-block.sp-header {
  padding-top: 85px;
}

.overflow-hidden {
  overflow: hidden;
}

html.overflow-hidden body {
  overflow: hidden;
}

/*==========  MEDIA QUERIES  ==========*/
@media (max-width: 768px) {
  #content-block.sp-header {
    padding-top: 68px;
  }
}
/*-------------------------------------------------------------------------------------------------------------------------------*/
/* ANIMATIONS */
/*-------------------------------------------------------------------------------------------------------------------------------*/
.simple-animation {
  transform: translate3d(0, -50px, 0);
  opacity: 0;
  transition: all 0.5s ease-out;
}

.bottom-animation {
  transform: translate3d(0, 50px, 0);
  opacity: 0;
  transition: all 0.5s ease-out;
}

.simple-animation.animated,
.bottom-animation.animated {
  transform: none;
  opacity: 1;
}

.delay-01 {
  transition-delay: 0.1s;
}

.delay-02 {
  transition-delay: 0.2s;
}

.delay-03 {
  transition-delay: 0.3s;
}

.delay-04 {
  transition-delay: 0.4s;
}

.delay-05 {
  transition-delay: 5s;
}

.delay-06 {
  transition-delay: 6s;
}

.delay-07 {
  transition-delay: 1.4s;
}

