/*==========  SIMPLE CONTENT  ==========*/
.text > * {
  margin-bottom: 12px;
}

.text > *:last-child {
  margin-bottom: 0;
}

.text img {
  max-width: 100%;
  height: auto;
  display: block;
}

.text ol {
  counter-reset: number;
}

.text ol li {
  padding-left: 0;
  margin-bottom: 15px;
  position: relative;
}

.text ol li:before {
  counter-increment: number;
  content: "0" counter(number) ".";
  margin-right: 8px;
  font-weight: 700;
  font-size: 14px;
  color: var(--dark);
}

.text ol li:last-of-type {
  margin-bottom: 0;
}

.text ul li {
  padding-left: 20px;
  margin-bottom: 2px;
  position: relative;
}

.text ul li:before {
  content: "";
  display: inline-block;
  background-color: #111111;
  background-repeat: no-repeat;
  background-size: contain;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  vertical-align: middle;
  position: absolute;
  left: 5px;
  top: 0.55em;
}

.text ul li:last-of-type {
  margin-bottom: 0;
}

.text blockquote {
  color: var(--dark);
  font-size: 20px;
  padding: 5px 5px 5px 25px;
  border-left: 4px solid var(--dark);
  font-weight: 600;
}

.text > iframe {
  width: 100%;
  height: 480px;
  max-height: calc(56.3vw - 30px);
}

.text table {
  border-left: 1px solid var(--dark);
  border-top: 1px solid var(--dark);
  border-spacing: 0;
  text-align: left;
}

.text table th,
.text table td {
  padding: 8px;
  border-right: 1px solid var(--dark);
  border-bottom: 1px solid var(--dark);
}

.text b {
  font-weight: var(--bold);
}

.text strong {
  font-weight: var(--bold);
}

.text a {
  text-decoration: none;
  color: var(--red);
}

.text h1, .text h2, .text h3, .text h4, .text h5, .text h6 {
  color: var(--white);
}

.text.color-white ul li:before {
  background-color: #fff;
}

.text.ul_type1 ul li {
  padding-left: 0;
}

.text.ul_type1 ul li:before {
  display: none;
}

.simple-text > * {
  margin-bottom: 25px;
}

.simple-text > *:last-child {
  margin-bottom: 0;
}

.simple-text img {
  max-width: 100%;
  height: auto;
  display: block;
}

.simple-text ol {
  counter-reset: number;
}

.simple-text ol li {
