/*==========  Header Style  ==========*/
:root {
  --width-navigation: 165px;
  --height-header: 60px;
  --sp-navigation: 15px;
}

.header {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  background-color: var(--gray-400);
  border-radius: 4px;
  z-index: 99;
}

.header__layer {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(31, 33, 36, 0.7);
  z-index: -2;
  transition: var(--base-transition);
  visibility: hidden;
  opacity: 0;
}

.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: var(--height-header);
  padding: 5px 0;
}

.header__inner:after {
  content: "";
  display: block;
  height: 100%;
  width: 100vw;
  background-color: var(--gray-400);
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: -1;
}

.header__inner .logo {
