/*==========  Checkbox Style  ==========*/
.checkbox-entry {
  display: inline-block;
  cursor: pointer;
  margin: 0;
  position: relative;
  padding-left: 18px;
  line-height: 1.4em;
  color: var(--white);
  transition: all 0.34s ease-out;
}

.checkbox-entry.checkbox-block {
  display: block;
  width: 100%;
}

.checkbox-entry .icon {
  line-height: 0;
  max-width: 80px;
}

.checkbox-entry .icon img {
  max-width: 100%;
}

.checkbox-entry input {
  display: none;
}

.checkbox-entry span {
  display: block;
  position: absolute;
  left: 0;
  top: 0.25em;
  width: 11px;
  height: 11px;
  border-radius: 2px;
  background-color: #fff;
}

.checkbox-entry span:after {
  content: "";
  position: absolute;
  left: 50%;
  top: 42%;
  transform: translate(-50%, -50%) rotate(45deg);
  width: 4px;
  height: 8px;
  z-index: 1;
  opacity: 0;
  visibility: hidden;
  border-right: 2px solid var(--red);
  border-bottom: 2px solid var(--red);
}

.checkbox-entry input:checked + span {
  border-color: #8768AD;
  background-color: var(--red);
}

.checkbox-entry input:checked + span:after {
  opacity: 1;
  visibility: visible;
  border-color: var(--white) !important;
}

.checkbox-entry.radio {
  padding-left: 22px;
}

.checkbox-entry input[type=radio] + span {
  width: 11px;
  height: 11px;
}

.checkbox-entry input[type=radio] + span:after {
  width: 5px;
  height: 5px;
  border: none;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.checkbox-entry input[type=radio] + span,
.checkbox-entry input[type=radio] + span:after {
  border-radius: 50%;
}

.checkbox-entry input[type=radio]:checked + span:after {
  opacity: 1;
  visibility: visible;
  background-color: var(--white) !important;
}

.checkbox-entry.disable {
  pointer-events: none;
}

.checkbox-entry.disable span:before {
  background-color: #F0F0F0;
}

.checkbox-entry.disable-checked {
