.program__banner2 {
  display: flex;
}

.program__banner2 .item {
  position: relative;
  min-height: 324px;
  overflow: hidden;
  flex: 0 0 86px;
  transition: 1s;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 32px;
}

.program__banner2 .item .bg {
  background-position: center;
}

.program__banner2 .item .title {
  font-size: 24px;
  line-height: 1.2em;
  font-weight: 700;
  color: rgba(255, 255, 255, 0.5);
  position: relative;
  width: 280px;
  flex: 0 0 280px;
  transform: rotate(-90deg);
  transform-origin: 26% 160%;
  transition: 1s;
}

.program__banner2 .item .image {
  position: relative;
  line-height: 0;
  text-align: right;
  padding-left: 50px;
  width: calc(100% - 280px);
}

.program__banner2 .item .image img {
  max-width: 100%;
}

.program__banner2 .item.active {
  flex: 0 0 calc(100% - 172px);
}

.program__banner2 .item.active .title {
  font-size: 32px;
  color: var(--white);
  transform: none;
}

.thumbnail-package {
  padding: 24px;
  background: #2D363F;
  border-radius: 5px;
  height: 100%;
}

.thumbnail-package .title {
  font-size: 18px;
  font-weight: 700;
  color: var(--white);
  margin-bottom: 24px;
}

.thumbnail-package__price {
  display: flex;
  flex-wrap: wrap;
  padding-bottom: 16px;
  border-bottom: 1px solid var(--gray-200);
  margin-bottom: 16px;
}

.thumbnail-package__price .item {
  background-color: var(--gray-200);
  padding: 9px 17px;
  border-radius: 5px;
  color: var(--white);
  font-size: 14px;
  font-weight: 500;
  margin-right: 8px;
  margin-bottom: 8px;
}

.thumbnail-package__items {
  display: flex;
  flex-wrap: wrap;
  border-top: 1px solid var(--gray-200);
  padding-top: 16px;
  margin-bottom: -8px;
}

.thumbnail-package__items .item {
  background-color: var(--gray-200);
  padding: 8px 17px 8px 38px;
  border-radius: 5px;
  color: var(--white);
  font-size: 14px;
  font-weight: 500;
  margin-right: 8px;
  margin-bottom: 8px;
  position: relative;
}

.thumbnail-package__items .item:after {
  content: "";
  display: block;
  width: 12px;
  height: 12px;
  position: absolute;
  left: 16px;
  top: 50%;
  transform: translateY(-50%);
  background-image: url("../images/icons/icon-0938-ok.svg");
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}

.thumbnail-package__items .item.green {
  color: var(--green);
}

.thumbnail-package__items .item.red {
  color: var(--red);
}

.thumbnail-package__items .item.red:after {
  background-image: url("../images/icons/icon-close-0393.svg");
}

.thumbnail-package__info {
  font-size: 14px;
  font-weight: 500;
  color: var(--white);
  position: relative;
  padding-left: 32px;
  margin-bottom: 16px;
}

.thumbnail-package__info .icon {
  display: block;
  width: 12px;
  line-height: 0;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 10px;
}

.thumbnail-package__info .icon img {
  max-width: 100%;
}

.thumbnail-package__wrapper {
  margin-bottom: -20px;
}

.thumbnail-package__wrapper [class*=col-] {
  margin-bottom: 20px;
}

