}

.switch-lang-wrapper li {
  display: flex;
  align-items: center;
  cursor: pointer;
  margin-right: 14px;
  margin-left: 14px;
  flex: 0 0 48px;
  position: relative;
}

.switch-lang-wrapper li .icon {
  line-height: 0;
  overflow: hidden;
  border-radius: 50%;
  width: 100%;
  display: block;
  transition: var(--base-transition);
}

