  width: 100%;
  transform: scale(1.25);
}

.switch-lang-wrapper li:after {
  content: "";
  display: block;
  height: 2px;
  width: 10px;
  border-radius: 4px;
  background-color: var(--red);
  position: absolute;
  left: 50%;
  bottom: -10px;
  transform: translateX(-50%);
  opacity: 0;
  visibility: hidden;
}

.switch-lang-wrapper li.active .icon {
  box-shadow: 0px 0px 20px #F81B1B;
}

.switch-lang-wrapper li.active:after {
  opacity: 1;
  visibility: visible;
}

