.dropdown2-list li:last-child {
  margin-bottom: 0;
}

.dropdown2-list li.active {
  background-color: rgba(1, 1, 1, 0.1);
}

.dropdown2-list a.item {
  display: block;
  font-size: 12px;
  color: var(--white);
  padding-left: 20px;
  position: relative;
  text-decoration: none;
}

.dropdown2-list a.item .icon {
  display: block;
  width: 12px;
  line-height: 0;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}

.dropdown2-list a.item .icon img {
  max-width: 100%;
}

.dropdown2-layer {
  background: rgba(0, 0, 0, 0);
  bottom: 0;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: none;
  z-index: 2;
}

.dropdown2.active .dropdown2-content {
  transform: none;
  visibility: visible;
  opacity: 1;
}

.dropdown2.active .dropdown2-layer {
  display: block;
}

.dropdown3 {
  display: block;
  position: relative;
}

.dropdown3-inner {
  font-size: 12px;
  color: var(--gray-600);
  background: var(--gray-200);
  border-radius: 5px;
  padding: 10px 65px 10px 15px;
}

.dropdown3-inner .arrow {
  width: 16px;
  line-height: 0;
  position: absolute;
  top: 50%;
  right: 15px;
  transform: translateY(-50%);
}

.dropdown3-inner .arrow img {
  width: 100%;
}

.dropdown3-trigger {
  background-color: #ff2929;
  background-image: url("../images/icons/icon-arrow-093p-30.svg");
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: 10px 4px;
  border-radius: 50%;
  box-shadow: 0 2px 5px #242b35;
  height: 18px;
  position: absolute;
  right: 15px;
  top: 50%;
  transform: translateY(-50%);
  transition: var(--base-transition);
  width: 18px;
  z-index: 6;
}

.dropdown3-item {
  font-size: 12px;
  color: var(--white);
  padding-left: 20px;
  position: relative;
}

.dropdown3-item .icon {
  display: block;
  width: 12px;
  line-height: 0;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}

.dropdown3-item .icon img {
  max-width: 100%;
}

.dropdown3-content {
  background-color: var(--gray-200);
  border-radius: 5px;
  min-width: 100%;
  padding: 10px 50px 10px 15px;
  position: absolute;
  left: 0;
  transform: translate(0, 0);
  top: calc(100% - 2px);
  visibility: hidden;
  opacity: 0;
  transition: var(--base-transition);
  z-index: 4;
}

.dropdown3-content > .text {
  max-height: 54px;
  overflow-y: auto;
}

.dropdown3-layer {
  background: rgba(0, 0, 0, 0);
  bottom: 0;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: none;
  z-index: 2;
}

.dropdown3.active .dropdown3-trigger {
  transform: translateY(-50%) rotate(180deg);
}

.dropdown3.active .dropdown3-content {
  /* transform: none; */
  visibility: visible;
  opacity: 1;
  top: 50%;
  transform: translateY(-50%);
}

.dropdown3.active .dropdown3-layer {
  display: block;
}

/* Pagination */
.pagination {
  display: flex;
  justify-content: center;
