.pagination__btn.prev {
  margin-right: 18px;
}

.pagination__btn.prev:after {
  transform: translate(-50%, -50%) rotate(180deg);
}

.pagination__btn svg {
  width: 10px;
  height: 9px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

